.nav {
    width: 300px;
    display: flex;
    justify-content: space-between;
    background: #2b2b2b;
    a {
        color: #d0d0d0;
        font-size: 14px;
        line-height: 45px;
        text-transform: uppercase;
        font-weight: 700;
        text-decoration: none;
    }
}

